// Definizione del tipo per gli eventi del calendario
type EventType = 'interview' | 'follow-up' | 'preparation' | 'learning' | 'deadline';

interface CalendarEvent {
  id: number;
  title: string;
  date: string;
  time: string;
  type: EventType;
  description: string;
  relatedTo?: {
    type: string;
    id: number;
    name: string;
  };
}

// Definizione del tipo per i template email
interface EmailTemplate {
  id: number;
  title: string;
  subject: string;
  body: string;
  variables: string[];
  category: 'follow-up' | 'interview' | 'offer' | 'rejection' | 'general';
}

