const RemindersCalendar: React.FC = () => {
  const [activeTab, setActiveTab] = React.useState('reminders');
  
  const handleTabChange = (tab: string) => {
    setActiveTab(tab);
  };
  
  return (
    <PageContainer>
      <PageHeader>
        <PageTitle>Promemoria e Calendario</PageTitle>
        <HeaderActions>
          <NotificationCenter />
        </HeaderActions>
      </PageHeader>
      
      <Tabs>
        <TabButton 
          active={activeTab === 'reminders'} 
          onClick={() => handleTabChange('reminders')}
        >
          Promemoria
        </TabButton>
        <TabButton 
          active={activeTab === 'calendar'} 
          onClick={() => handleTabChange('calendar')}
        >
          Calendario
        </TabButton>
        <TabButton 
          active={activeTab === 'templates'} 
          onClick={() => handleTabChange('templates')}
        >
          Template Email
        </TabButton>
      </Tabs>
      
      <TabContent>
        {activeTab === 'reminders' && (
          <RemindersDashboard>
            <DashboardSection>
              <SectionHeader>
                <SectionTitle>Promemoria in arrivo</SectionTitle>
                <AddButton>+ Nuovo Promemoria</AddButton>
              </SectionHeader>
              
              <RemindersList>
                {calendarEvents.map(event => (
                  <ReminderItem key={event.id}>
                    <ReminderIconContainer type={event.type}>
                      {event.type === 'interview' ? '🗓️' : 
                       event.type === 'follow-up' ? '📧' : 
                       event.type === 'preparation' ? '📝' : 
                       event.type === 'learning' ? '📚' : 
                       event.type === 'deadline' ? '⏰' : '📌'}
                    </ReminderIconContainer>
                    
                    <ReminderContent>
                      <ReminderTitle>{event.title}</ReminderTitle>
                      <ReminderDescription>{event.description}</ReminderDescription>
                      <ReminderMeta>
                        <ReminderDate>
                          {new Date(event.date).toLocaleDateString('it-IT')} alle {event.time}
                        </ReminderDate>
                        {event.relatedTo && (
                          <ReminderRelated>
                            {event.relatedTo.name}
                          </ReminderRelated>
                        )}
                      </ReminderMeta>
                    </ReminderContent>
                    
                    <ReminderActions>
                      <ReminderAction>Completa</ReminderAction>
                      <ReminderAction>Posticipa</ReminderAction>
                    </ReminderActions>
                  </ReminderItem>
                ))}
              </RemindersList>
            </DashboardSection>
            
            <DashboardSection>
              <SectionHeader>
                <SectionTitle>Automazioni attive</SectionTitle>
                <AddButton>+ Nuova Automazione</AddButton>
              </SectionHeader>
              
              <AutomationsList>
                <AutomationItem>
                  <AutomationIcon>🔄</AutomationIcon>
                  <AutomationContent>
                    <AutomationTitle>Follow-up post candidatura</AutomationTitle>
                    <AutomationDescription>
                      Invia promemoria 7 giorni dopo l'invio della candidatura
                    </AutomationDescription>
                  </AutomationContent>
                  <AutomationToggle>
                    <ToggleSwitch checked={true} />
                  </AutomationToggle>
                </AutomationItem>
                
                <AutomationItem>
                  <AutomationIcon>🔄</AutomationIcon>
                  <AutomationContent>
                    <AutomationTitle>Preparazione colloquio</AutomationTitle>
                    <AutomationDescription>
                      Invia promemoria 2 giorni prima di un colloquio programmato
                    </AutomationDescription>
                  </AutomationContent>
                  <AutomationToggle>
                    <ToggleSwitch checked={true} />
                  </AutomationToggle>
                </AutomationItem>
                
                <AutomationItem>
                  <AutomationIcon>🔄</AutomationIcon>
                  <AutomationContent>
                    <AutomationTitle>Follow-up post colloquio</AutomationTitle>
                    <AutomationDescription>
                      Invia promemoria 2 giorni dopo un colloquio
                    </AutomationDescription>
                  </AutomationContent>
                  <AutomationToggle>
                    <ToggleSwitch checked={true} />
                  </AutomationToggle>
                </AutomationItem>
              </AutomationsList>
            </DashboardSection>
          </RemindersDashboard>
        )}
        
        {activeTab === 'calendar' && (
          <CalendarComponent events={calendarEvents} />
        )}
        
        {activeTab === 'templates' && (
          <EmailTemplateEditor templates={emailTemplates} />
        )}
      </TabContent>
    </PageContainer>
  );
};

