import React, { useState } from 'react';
import styled from 'styled-components';
import { Edit, Copy, Trash, Send } from 'react-feather';

interface EmailTemplate {
  id: number;
  title: string;
  subject: string;
  body: string;
  variables: string[];
  category: 'follow-up' | 'interview' | 'offer' | 'rejection' | 'general';
}

interface EmailTemplateEditorProps {
  templates?: EmailTemplate[];
  onSaveTemplate?: (template: EmailTemplate) => void;
  onDeleteTemplate?: (id: number) => void;
  onUseTemplate?: (template: EmailTemplate) => void;
}

const EmailTemplateEditor: React.FC<EmailTemplateEditorProps> = ({
  templates = [],
  onSaveTemplate,
  onDeleteTemplate,
  onUseTemplate
}) => {
  const [activeTab, setActiveTab] = useState<'library' | 'editor'>('library');
  const [selectedTemplate, setSelectedTemplate] = useState<EmailTemplate | null>(null);
  const [editMode, setEditMode] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [categoryFilter, setCategoryFilter] = useState<string | null>(null);
  
  // Filter templates based on search term and category
  const filteredTemplates = templates.filter(template => {
    const matchesSearch = template.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         template.subject.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         template.body.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesCategory = categoryFilter ? template.category === categoryFilter : true;
    
    return matchesSearch && matchesCategory;
  });
  
  // Create a new template
  const createNewTemplate = () => {
    const newTemplate: EmailTemplate = {
      id: Date.now(),
      title: 'Nuovo Template',
      subject: '',
      body: '',
      variables: [],
      category: 'general'
    };
    
    setSelectedTemplate(newTemplate);
    setEditMode(true);
    setActiveTab('editor');
  };
  
  // Edit existing template
  const editTemplate = (template: EmailTemplate) => {
    setSelectedTemplate(template);
    setEditMode(true);
    setActiveTab('editor');
  };
  
  // Duplicate template
  const duplicateTemplate = (template: EmailTemplate) => {
    const duplicatedTemplate: EmailTemplate = {
      ...template,
      id: Date.now(),
      title: `${template.title} (copia)`
    };
    
    if (onSaveTemplate) {
      onSaveTemplate(duplicatedTemplate);
    }
  };
  
  // Delete template
  const deleteTemplate = (id: number) => {
    if (onDeleteTemplate) {
      onDeleteTemplate(id);
    }
  };
  
  // Handle use template
  const handleUseTemplate = (template: EmailTemplate) => {
    if (onUseTemplate) {
      onUseTemplate(template);
    }
  };
  
  // Save template
  const saveTemplate = () => {
    if (selectedTemplate && onSaveTemplate) {
      onSaveTemplate(selectedTemplate);
      setEditMode(false);
    }
  };
  
  // Cancel editing
  const cancelEditing = () => {
    setEditMode(false);
    if (!selectedTemplate?.id) {
      setSelectedTemplate(null);
    }
  };
  
  // Update template field
  const updateTemplateField = (field: keyof EmailTemplate, value: any) => {
    if (selectedTemplate) {
      setSelectedTemplate({
        ...selectedTemplate,
        [field]: value
      });
    }
  };
  
  // Add variable to template
  const addVariable = (variable: string) => {
    if (selectedTemplate) {
      if (!selectedTemplate.variables.includes(variable)) {
        updateTemplateField('variables', [...selectedTemplate.variables, variable]);
      }
    }
  };
  
  // Remove variable from template
  const removeVariable = (variable: string) => {
    if (selectedTemplate) {
      updateTemplateField('variables', selectedTemplate.variables.filter(v => v !== variable));
    }
  };
  
  // Get category label
  const getCategoryLabel = (category: string) => {
    switch (category) {
      case 'follow-up':
        return 'Follow-up';
      case 'interview':
        return 'Colloquio';
      case 'offer':
        return 'Offerta';
      case 'rejection':
        return 'Rifiuto';
      case 'general':
        return 'Generale';
      default:
        return category;
    }
  };
  
  // Get category color
  const getCategoryColor = (category: string) => {
    switch (category) {
      case 'follow-up':
        return '#2EB89F'; // primary
      case 'interview':
        return '#3B82F6'; // blue
      case 'offer':
        return '#10B981'; // green
      case 'rejection':
        return '#EF4444'; // red
      case 'general':
        return '#6B7280'; // gray
      default:
        return '#6B7280'; // gray
    }
  };
  
  // Common variables
  const commonVariables = [
    'position',
    'company',
    'recruiterName',
    'applicationDate',
    'interviewDate',
    'yourName',
    'yourEmail',
    'yourPhone'
  ];
  
  return (
    <TemplateEditorContainer>
      <TemplateEditorHeader>
        <Tabs>
          <TabButton 
            active={activeTab === 'library'} 
            onClick={() => setActiveTab('library')}
          >
            Libreria Template
          </TabButton>
          <TabButton 
            active={activeTab === 'editor'} 
            onClick={() => setActiveTab('editor')}
          >
            {editMode ? 'Modifica Template' : 'Visualizza Template'}
          </TabButton>
        </Tabs>
        
        {activeTab === 'library' && (
          <CreateButton onClick={createNewTemplate}>
            + Nuovo Template
          </CreateButton>
        )}
        
        {activeTab === 'editor' && !editMode && selectedTemplate && (
          <EditorActions>
            <ActionButton onClick={() => editTemplate(selectedTemplate)}>
              <Edit size={16} />
              Modifica
            </ActionButton>
            <ActionButton onClick={() => handleUseTemplate(selectedTemplate)}>
              <Send size={16} />
              Usa Template
            </ActionButton>
          </EditorActions>
        )}
        
        {activeTab === 'editor' && editMode && (
          <EditorActions>
            <ActionButton onClick={saveTemplate}>
              Salva
            </ActionButton>
            <ActionButton secondary onClick={cancelEditing}>
              Annulla
            </ActionButton>
          </EditorActions>
        )}
      </TemplateEditorHeader>
      
      {activeTab === 'library' && (
        <LibraryContent>
          <SearchFilters>
            <SearchInput 
              type="text" 
              placeholder="Cerca template..." 
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
            
            <CategoryFilters>
              <CategoryFilter 
                active={categoryFilter === null} 
                onClick={() => setCategoryFilter(null)}
              >
                Tutti
              </CategoryFilter>
              <CategoryFilter 
                active={categoryFilter === 'follow-up'} 
                color={getCategoryColor('follow-up')}
                onClick={() => setCategoryFilter('follow-up')}
              >
                Follow-up
              </CategoryFilter>
              <CategoryFilter 
                active={categoryFilter === 'interview'} 
                color={getCategoryColor('interview')}
                onClick={() => setCategoryFilter('interview')}
              >
                Colloquio
              </CategoryFilter>
              <CategoryFilter 
                active={categoryFilter === 'offer'} 
                color={getCategoryColor('offer')}
                onClick={() => setCategoryFilter('offer')}
              >
                Offerta
              </CategoryFilter>
              <CategoryFilter 
                active={categoryFilter === 'rejection'} 
                color={getCategoryColor('rejection')}
                onClick={() => setCategoryFilter('rejection')}
              >
                Rifiuto
              </CategoryFilter>
              <CategoryFilter 
                active={categoryFilter === 'general'} 
                color={getCategoryColor('general')}
                onClick={() => setCategoryFilter('general')}
              >
                Generale
              </CategoryFilter>
            </CategoryFilters>
          </SearchFilters>
          
          <TemplatesList>
            {filteredTemplates.length > 0 ? (
              filteredTemplates.map(template => (
                <TemplateItem key={template.id}>
                  <TemplateHeader>
                    <TemplateTitle onClick={() => {
                      setSelectedTemplate(template);
                      setActiveTab('editor');
                    }}>
                      {template.title}
                    </TemplateTitle>
                    <TemplateCategory color={getCategoryColor(template.category)}>
                      {getCategoryLabel(template.category)}
                    </TemplateCategory>
                  </TemplateHeader>
                  
                  <TemplateSubject>{template.subject}</TemplateSubject>
                  
                  <TemplatePreview>
                    {template.body.substring(0, 150)}...
                  </TemplatePreview>
                  
                  <TemplateFooter>
                    <TemplateActions>
                      <TemplateAction onClick={() => editTemplate(template)}>
                        <Edit size={16} />
                      </TemplateAction>
                      <TemplateAction onClick={() => duplicateTemplate(template)}>
                        <Copy size={16} />
                      </TemplateAction>
                      <TemplateAction onClick={() => deleteTemplate(template.id)}>
                        <Trash size={16} />
                      </TemplateAction>
                    </TemplateActions>
                    
                    <UseTemplateButton onClick={() => handleUseTemplate(template)}>
                      Usa Template
                    </UseTemplateButton>
                  </TemplateFooter>
                </TemplateItem>
              ))
            ) : (
              <EmptyState>
                <EmptyIcon>📝</EmptyIcon>
                <EmptyTitle>Nessun template trovato</EmptyTitle>
                <EmptyDescription>
                  {searchTerm || categoryFilter ? 
                    'Prova a modificare i filtri di ricerca' : 
                    'Crea il tuo primo template email cliccando su "Nuovo Template"'
                  }
                </EmptyDescription>
                {!searchTerm && !categoryFilter && (
                  <CreateButton onClick={createNewTemplate}>
                    + Nuovo Template
                  </CreateButton>
                )}
              </EmptyState>
            )}
          </TemplatesList>
        </LibraryContent>
      )}
      
      {activeTab === 'editor' && selectedTemplate && (
        <EditorContent>
          <EditorForm>
            <FormGroup>
              <Label>Titolo Template</Label>
              <Input 
                type="text" 
                value={selectedTemplate.title}
                onChange={(e) => updateTemplateField('title', e.target.value)}
                disabled={!editMode}
              />
            </FormGroup>
            
            <FormGroup>
              <Label>Categoria</Label>
              <Select 
                value={selectedTemplate.category}
                onChange={(e) => updateTemplateField('category', e.target.value)}
                disabled={!editMode}
              >
                <option value="follow-up">Follow-up</option>
                <option value="interview">Colloquio</option>
                <option value="offer">Offerta</option>
                <option value="rejection">Rifiuto</option>
                <option value="general">Generale</option>
              </Select>
            </FormGroup>
            
            <FormGroup>
              <Label>Oggetto Email</Label>
              <Input 
                type="text" 
                value={selectedTemplate.subject}
                onChange={(e) => updateTemplateField('subject', e.target.value)}
                disabled={!editMode}
              />
            </FormGroup>
            
            <FormGroup>
              <Label>Corpo Email</Label>
              <TextArea 
                value={selectedTemplate.body}
                onChange={(e) => updateTemplateField('body', e.target.value)}
                disabled={!editMode}
                rows={12}
              />
            </FormGroup>
          </EditorForm>
          
          <EditorSidebar>
            <SidebarSection>
              <SidebarTitle>Variabili Disponibili</SidebarTitle>
              <VariablesList>
                {commonVariables.map(variable => (
                  <VariableItem 
                    key={variable}
                    onClick={() => editMode && addVariable(variable)}
                    active={selectedTemplate.variables.includes(variable)}
                    disabled={!editMode}
                  >
                    {`{{${variable}}}`}
                  </VariableItem>
                ))}
              </VariablesList>
            </SidebarSection>
            
            <SidebarSection>
              <SidebarTitle>Variabili Utilizzate</SidebarTitle>
              {selectedTemplate.variables.length > 0 ? (
                <VariablesList>
                  {selectedTemplate.variables.map(variable => (
                    <VariableItem 
                      key={variable}
                      onClick={() => editMode && removeVariable(variable)}
                      active={true}
                      disabled={!editMode}
                    >
                      {`{{${variable}}}`}
                      {editMode && <RemoveIcon>×</RemoveIcon>}
                    </VariableItem>
                  ))}
                </VariablesList>
              ) : (
                <EmptyVariables>
                  Nessuna variabile utilizzata
                </EmptyVariables>
              )}
            </SidebarSection>
            
            <SidebarSection>
              <SidebarTitle>Anteprima</SidebarTitle>
              <PreviewButton onClick={() => alert('Anteprima non disponibile in questa versione')}>
                Visualizza Anteprima
              </PreviewButton>
            </SidebarSection>
          </EditorSidebar>
        </EditorContent>
      )}
    </TemplateEditorContainer>
  );
};

