import React, { useState } from 'react';
import styled from 'styled-components';
import { Link, useNavigate } from 'react-router-dom';
import Button from '../../components/common/Button';

const Register = () => {
  const [step, setStep] = useState(1);
  const [userType, setUserType] = useState('candidate'); // 'candidate' o 'company'
  const navigate = useNavigate();
  
  // Form data
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    password: '',
    confirmPassword: '',
    companyName: '',
    role: '',
    acceptTerms: false
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setFormData({
      ...formData,
      [name]: type === 'checkbox' ? checked : value
    });
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (step < 2) {
      setStep(step + 1);
    } else {
      // In una vera implementazione, qui ci sarebbe la chiamata API per la registrazione
      console.log('Registrazione con:', { ...formData, userType });
      
      // Simuliamo una registrazione di successo e redirezioniamo alla dashboard
      navigate('/dashboard');
    }
  };

  const handleBack = () => {
    setStep(step - 1);
  };

  return (
    <RegisterContainer>
      <RegisterCard>
        <LogoContainer>
          <Logo src="/logo.png" alt="Jobfolio" />
        </LogoContainer>
        
        <WelcomeText>Crea il tuo account</WelcomeText>
        <SubText>Inizia il tuo percorso con Jobfolio</SubText>
        
        <UserTypeToggle>
          <UserTypeButton 
            active={userType === 'candidate'} 
            onClick={() => setUserType('candidate')}
          >
            Candidato
          </UserTypeButton>
          <UserTypeButton 
            active={userType === 'company'} 
            onClick={() => setUserType('company')}
          >
            Azienda
          </UserTypeButton>
        </UserTypeToggle>
        
        <StepIndicator>
          <StepDot active={step >= 1} />
          <StepLine active={step >= 2} />
          <StepDot active={step >= 2} />
        </StepIndicator>
        
        <RegisterForm onSubmit={handleSubmit}>
          {step === 1 ? (
            <>
              <StepTitle>Informazioni di base</StepTitle>
              
              {userType === 'candidate' ? (
                <>
                  <FormRow>
                    <FormGroup>
                      <Label htmlFor="firstName">Nome</Label>
                      <Input 
                        id="firstName"
                        name="firstName"
                        type="text" 
                        placeholder="Inserisci il tuo nome"
                        value={formData.firstName}
                        onChange={handleChange}
                        required
                      />
                    </FormGroup>
                    
                    <FormGroup>
                      <Label htmlFor="lastName">Cognome</Label>
                      <Input 
                        id="lastName"
                        name="lastName"
                        type="text" 
                        placeholder="Inserisci il tuo cognome"
                        value={formData.lastName}
                        onChange={handleChange}
                        required
                      />
                    </FormGroup>
                  </FormRow>
                  
                  <FormGroup>
                    <Label htmlFor="role">Ruolo professionale</Label>
                    <Input 
                      id="role"
                      name="role"
                      type="text" 
                      placeholder="Es. Sviluppatore Frontend, Product Manager"
                      value={formData.role}
                      onChange={handleChange}
                      required
                    />
                  </FormGroup>
                </>
              ) : (
                <FormGroup>
                  <Label htmlFor="companyName">Nome azienda</Label>
                  <Input 
                    id="companyName"
                    name="companyName"
                    type="text" 
                    placeholder="Inserisci il nome dell'azienda"
                    value={formData.companyName}
                    onChange={handleChange}
                    required
                  />
                </FormGroup>
              )}
              
              <FormGroup>
                <Label htmlFor="email">Email</Label>
                <Input 
                  id="email"
                  name="email"
                  type="email" 
                  placeholder="Inserisci la tua email"
                  value={formData.email}
                  onChange={handleChange}
                  required
                />
              </FormGroup>
            </>
          ) : (
            <>
              <StepTitle>Sicurezza</StepTitle>
              
              <FormGroup>
                <Label htmlFor="password">Password</Label>
                <Input 
                  id="password"
                  name="password"
                  type="password" 
                  placeholder="Crea una password sicura"
                  value={formData.password}
                  onChange={handleChange}
                  required
                />
              </FormGroup>
              
              <FormGroup>
                <Label htmlFor="confirmPassword">Conferma password</Label>
                <Input 
                  id="confirmPassword"
                  name="confirmPassword"
                  type="password" 
                  placeholder="Conferma la tua password"
                  value={formData.confirmPassword}
                  onChange={handleChange}
                  required
                />
              </FormGroup>
              
              <CheckboxContainer>
                <Checkbox 
                  id="acceptTerms"
                  name="acceptTerms"
                  type="checkbox"
                  checked={formData.acceptTerms}
                  onChange={handleChange}
                  required
                />
                <CheckboxLabel htmlFor="acceptTerms">
                  Accetto i <TermsLink to="/terms">Termini di Servizio</TermsLink> e la <TermsLink to="/privacy">Privacy Policy</TermsLink>
                </CheckboxLabel>
              </CheckboxContainer>
            </>
          )}
          
          <ButtonGroup>
            {step > 1 && (
              <BackButton type="button" variant="outline" onClick={handleBack}>
                Indietro
              </BackButton>
            )}
            <NextButton type="submit" variant="primary" fullWidth={step === 1}>
              {step < 2 ? 'Continua' : 'Registrati'}
            </NextButton>
          </ButtonGroup>
        </RegisterForm>
        
        <Divider>
          <DividerText>oppure</DividerText>
        </Divider>
        
        <SocialLogin>
          <SocialButton type="button">
            <SocialIcon src="/google-icon.png" alt="Google" />
            Registrati con Google
          </SocialButton>
          
          <SocialButton type="button">
            <SocialIcon src="/facebook-icon.png" alt="Facebook" />
            Registrati con Facebook
          </SocialButton>
        </SocialLogin>
        
        <LoginPrompt>
          Hai già un account? <LoginLink to="/login">Accedi</LoginLink>
        </LoginPrompt>
      </RegisterCard>
      
      <ImageSection>
        <ImageOverlay />
        <RegisterImage src={userType === 'candidate' ? "/register-candidate.jpg" : "/register-company.jpg"} alt="Jobfolio" />
        <ImageContent>
          <ImageTitle>
            {userType === 'candidate' 
              ? 'Inizia il tuo percorso professionale' 
              : 'Trova i talenti giusti per la tua azienda'}
          </ImageTitle>
          <ImageText>
            {userType === 'candidate'
              ? 'Crea il tuo profilo, scopri le tue competenze da migliorare e trova il lavoro dei tuoi sogni'
              : 'Pubblica offerte di lavoro, trova candidati qualificati e gestisci il processo di selezione in modo efficiente'}
          </ImageText>
        </ImageContent>
      </ImageSection>
    </RegisterContainer>
  );
};

// Styled Components
const RegisterContainer = styled.div`
  display: flex;
  min-height: 100vh;
  
  @media (max-width: ${props => props.theme.breakpoints.lg}) {
    flex-direction: column;
  }
`;

const RegisterCard = styled.div`
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 3rem;
  justify-content: center;
  max-width: 600px;
  
  @media (max-width: ${props => props.theme.breakpoints.lg}) {
    max-width: 100%;
    padding: 2rem;
  }
`;

const LogoContainer = styled.div`
  margin-bottom: 2.5rem;
`;

const Logo = styled.img`
  height: 40px;
  width: auto;
`;

