import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Calendar as CalendarIcon, ChevronLeft, ChevronRight } from 'react-feather';

interface CalendarEvent {
  id: number;
  title: string;
  date: string; // YYYY-MM-DD
  time: string; // HH:MM
  type: 'interview' | 'follow-up' | 'deadline' | 'preparation' | 'learning';
  description?: string;
  relatedTo?: {
    type: string;
    id: number;
    name: string;
  };
}

interface CalendarProps {
  events?: CalendarEvent[];
  onEventClick?: (event: CalendarEvent) => void;
  onDateSelect?: (date: Date) => void;
}

const CalendarComponent: React.FC<CalendarProps> = ({ 
  events = [], 
  onEventClick,
  onDateSelect
}) => {
  const [currentDate, setCurrentDate] = useState(new Date());
  const [selectedDate, setSelectedDate] = useState(new Date());
  const [viewMode, setViewMode] = useState<'month' | 'week' | 'day'>('month');
  
  // Get events for selected date
  const selectedDateString = selectedDate.toISOString().split('T')[0];
  const eventsForSelectedDate = events.filter(event => event.date === selectedDateString);
  
  // Navigate to previous month/week/day
  const navigatePrevious = () => {
    const newDate = new Date(currentDate);
    if (viewMode === 'month') {
      newDate.setMonth(newDate.getMonth() - 1);
    } else if (viewMode === 'week') {
      newDate.setDate(newDate.getDate() - 7);
    } else {
      newDate.setDate(newDate.getDate() - 1);
    }
    setCurrentDate(newDate);
  };
  
  // Navigate to next month/week/day
  const navigateNext = () => {
    const newDate = new Date(currentDate);
    if (viewMode === 'month') {
      newDate.setMonth(newDate.getMonth() + 1);
    } else if (viewMode === 'week') {
      newDate.setDate(newDate.getDate() + 7);
    } else {
      newDate.setDate(newDate.getDate() + 1);
    }
    setCurrentDate(newDate);
  };
  
  // Navigate to today
  const navigateToday = () => {
    setCurrentDate(new Date());
    setSelectedDate(new Date());
    if (onDateSelect) {
      onDateSelect(new Date());
    }
  };
  
  // Handle date selection
  const handleDateSelect = (date: Date) => {
    setSelectedDate(date);
    if (onDateSelect) {
      onDateSelect(date);
    }
  };
  
  // Get month name and year
  const getMonthName = (date: Date) => {
    return date.toLocaleString('it-IT', { month: 'long', year: 'numeric' });
  };
  
  // Get week range
  const getWeekRange = (date: Date) => {
    const startOfWeek = new Date(date);
    const day = startOfWeek.getDay();
    const diff = startOfWeek.getDate() - day + (day === 0 ? -6 : 1); // Adjust for Sunday
    startOfWeek.setDate(diff);
    
    const endOfWeek = new Date(startOfWeek);
    endOfWeek.setDate(endOfWeek.getDate() + 6);
    
    return `${startOfWeek.getDate()} - ${endOfWeek.getDate()} ${startOfWeek.toLocaleString('it-IT', { month: 'long', year: 'numeric' })}`;
  };
  
  // Get day name and date
  const getDayName = (date: Date) => {
    return date.toLocaleString('it-IT', { weekday: 'long', day: 'numeric', month: 'long', year: 'numeric' });
  };
  
  // Get current view title
  const getCurrentViewTitle = () => {
    if (viewMode === 'month') {
      return getMonthName(currentDate);
    } else if (viewMode === 'week') {
      return getWeekRange(currentDate);
    } else {
      return getDayName(currentDate);
    }
  };
  
  // Generate days for month view
  const generateMonthDays = () => {
    const year = currentDate.getFullYear();
    const month = currentDate.getMonth();
    
    // Get first day of month
    const firstDay = new Date(year, month, 1);
    const firstDayOfWeek = firstDay.getDay() || 7; // Convert Sunday (0) to 7
    
    // Get last day of month
    const lastDay = new Date(year, month + 1, 0);
    const daysInMonth = lastDay.getDate();
    
    // Get days from previous month
    const daysFromPrevMonth = firstDayOfWeek - 1;
    const prevMonth = new Date(year, month, 0);
    const prevMonthDays = prevMonth.getDate();
    
    // Get days for next month
    const daysFromNextMonth = 42 - daysInMonth - daysFromPrevMonth;
    
    const days = [];
    
    // Add days from previous month
    for (let i = daysFromPrevMonth - 1; i >= 0; i--) {
      const date = new Date(year, month - 1, prevMonthDays - i);
      days.push({
        date,
        isCurrentMonth: false,
        isToday: isSameDay(date, new Date()),
        isSelected: isSameDay(date, selectedDate),
        events: events.filter(event => event.date === date.toISOString().split('T')[0])
      });
    }
    
    // Add days from current month
    for (let i = 1; i <= daysInMonth; i++) {
      const date = new Date(year, month, i);
      days.push({
        date,
        isCurrentMonth: true,
        isToday: isSameDay(date, new Date()),
        isSelected: isSameDay(date, selectedDate),
        events: events.filter(event => event.date === date.toISOString().split('T')[0])
      });
    }
    
    // Add days from next month
    for (let i = 1; i <= daysFromNextMonth; i++) {
      const date = new Date(year, month + 1, i);
      days.push({
        date,
        isCurrentMonth: false,
        isToday: isSameDay(date, new Date()),
        isSelected: isSameDay(date, selectedDate),
        events: events.filter(event => event.date === date.toISOString().split('T')[0])
      });
    }
    
    return days;
  };
  
  // Check if two dates are the same day
  const isSameDay = (date1: Date, date2: Date) => {
    return date1.getDate() === date2.getDate() &&
           date1.getMonth() === date2.getMonth() &&
           date1.getFullYear() === date2.getFullYear();
  };
  
  // Get event color based on type
  const getEventColor = (type: string) => {
    switch (type) {
      case 'interview':
        return '#3B82F6'; // blue
      case 'follow-up':
        return '#2EB89F'; // primary
      case 'deadline':
        return '#EF4444'; // red
      case 'preparation':
        return '#F6C046'; // yellow
      case 'learning':
        return '#8B5CF6'; // purple
      default:
        return '#6B7280'; // gray
    }
  };
  
  return (
    <CalendarContainer>
      <CalendarHeader>
        <MonthNavigation>
          <NavButton onClick={navigatePrevious}>
            <ChevronLeft size={20} />
          </NavButton>
          <CurrentMonth>{getCurrentViewTitle()}</CurrentMonth>
          <NavButton onClick={navigateNext}>
            <ChevronRight size={20} />
          </NavButton>
        </MonthNavigation>
        
        <ViewControls>
          <ViewButton 
            active={viewMode === 'month'} 
            onClick={() => setViewMode('month')}
          >
            Mese
          </ViewButton>
          <ViewButton 
            active={viewMode === 'week'} 
            onClick={() => setViewMode('week')}
          >
            Settimana
          </ViewButton>
          <ViewButton 
            active={viewMode === 'day'} 
            onClick={() => setViewMode('day')}
          >
            Giorno
          </ViewButton>
          <TodayButton onClick={navigateToday}>Oggi</TodayButton>
        </ViewControls>
      </CalendarHeader>
      
      {viewMode === 'month' && (
        <MonthView>
          <WeekdayHeader>
            <WeekdayCell>Lun</WeekdayCell>
            <WeekdayCell>Mar</WeekdayCell>
            <WeekdayCell>Mer</WeekdayCell>
            <WeekdayCell>Gio</WeekdayCell>
            <WeekdayCell>Ven</WeekdayCell>
            <WeekdayCell>Sab</WeekdayCell>
            <WeekdayCell>Dom</WeekdayCell>
          </WeekdayHeader>
          
          <DaysGrid>
            {generateMonthDays().map((day, index) => (
              <DayCell 
                key={index}
                isCurrentMonth={day.isCurrentMonth}
                isToday={day.isToday}
                isSelected={day.isSelected}
                onClick={() => handleDateSelect(day.date)}
              >
                <DayNumber>{day.date.getDate()}</DayNumber>
                <EventDots>
                  {day.events.slice(0, 3).map((event, eventIndex) => (
                    <EventDot 
                      key={eventIndex} 
                      color={getEventColor(event.type)}
                      onClick={(e) => {
                        e.stopPropagation();
                        if (onEventClick) onEventClick(event);
                      }}
                    />
                  ))}
                  {day.events.length > 3 && (
                    <MoreEvents>+{day.events.length - 3}</MoreEvents>
                  )}
                </EventDots>
              </DayCell>
            ))}
          </DaysGrid>
        </MonthView>
      )}
      
      {viewMode === 'week' && (
        <WeekView>
          <WeekViewPlaceholder>
            Visualizzazione settimanale in fase di implementazione
          </WeekViewPlaceholder>
        </WeekView>
      )}
      
      {viewMode === 'day' && (
        <DayView>
          <DayViewPlaceholder>
            Visualizzazione giornaliera in fase di implementazione
          </DayViewPlaceholder>
        </DayView>
      )}
      
      <EventsList>
        <EventsHeader>
          <EventsTitle>Eventi del {selectedDate.toLocaleDateString('it-IT')}</EventsTitle>
          <AddEventButton>+ Nuovo Evento</AddEventButton>
        </EventsHeader>
        
        {eventsForSelectedDate.length > 0 ? (
          <EventItems>
            {eventsForSelectedDate.map(event => (
              <EventItem 
                key={event.id}
                onClick={() => onEventClick && onEventClick(event)}
              >
                <EventTime>{event.time}</EventTime>
                <EventContent>
                  <EventTitle>{event.title}</EventTitle>
                  {event.description && (
                    <EventDescription>{event.description}</EventDescription>
                  )}
                  {event.relatedTo && (
                    <EventRelated>{event.relatedTo.name}</EventRelated>
                  )}
                </EventContent>
                <EventType color={getEventColor(event.type)} />
              </EventItem>
            ))}
          </EventItems>
        ) : (
          <NoEvents>
            <CalendarIcon size={40} color="#E5E7EB" />
            <NoEventsText>Nessun evento programmato per questa data</NoEventsText>
          </NoEvents>
        )}
      </EventsList>
      
      <SyncSection>
        <SyncHeader>Sincronizza con</SyncHeader>
        <SyncButtons>
          <SyncButton provider="google">
            <GoogleIcon />
            Google Calendar
          </SyncButton>
          <SyncButton provider="outlook">
            <OutlookIcon />
            Outlook
          </SyncButton>
          <SyncButton provider="apple">
            <AppleIcon />
            Apple Calendar
          </SyncButton>
        </SyncButtons>
      </SyncSection>
    </CalendarContainer>
  );
};

