import React, { useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

// Placeholder components - replace with actual implementations
const Sidebar = () => <SidebarContainer>Sidebar</SidebarContainer>;
const Header = () => <HeaderContainer>Header</HeaderContainer>;
const RadarChart = () => <RadarChartContainer>Radar Chart Placeholder</RadarChartContainer>;
const ProgressBar = ({ value }: { value: number }) => <ProgressBarContainer><Progress value={value} /></ProgressBarContainer>;
const Button = ({ children, primary }: { children: React.ReactNode, primary?: boolean }) => <StyledButton primary={primary}>{children}</StyledButton>;

const GapAnalyzer = () => {
  const [activeTab, setActiveTab] = useState('dashboard');
  const [selectedRole, setSelectedRole] = useState('frontend_developer');

  const roles = [
    { id: 'frontend_developer', name: 'Sviluppatore Frontend', category: 'tech' },
    { id: 'backend_developer', name: 'Sviluppatore Backend', category: 'tech' },
    { id: 'sales_manager', name: 'Sales Account Manager', category: 'sales' },
    { id: 'financial_analyst', name: 'Financial Analyst', category: 'finance' },
    { id: 'customer_success', name: 'Customer Success Manager', category: 'support' },
    { id: 'devops_engineer', name: 'DevOps Engineer', category: 'tech' },
    { id: 'ux_ui_designer', name: 'UX/UI Designer', category: 'creative' },
    { id: 'product_manager', name: 'Product Manager', category: 'management' },
    { id: 'digital_marketing', name: 'Digital Marketing Specialist', category: 'marketing' },
    { id: 'business_analyst', name: 'Business Analyst', category: 'management' },
  ];

  const skills = {
    frontend_developer: [
      { name: 'HTML/CSS', level: 4, required: 5 },
      { name: 'JavaScript', level: 4, required: 5 },
      { name: 'React', level: 3, required: 5 },
      { name: 'TypeScript', level: 2, required: 4 },
      { name: 'Testing', level: 2, required: 3 },
    ],
    sales_manager: [
      { name: 'CRM', level: 4, required: 5 },
      { name: 'Negoziazione', level: 4, required: 5 },
      { name: 'Lead Generation', level: 3, required: 4 },
      { name: 'Sales Funnel', level: 3, required: 4 },
      { name: 'Comunicazione', level: 5, required: 5 },
    ],
    // Add skills for other roles...
  };

  const currentSkills = skills[selectedRole as keyof typeof skills] || skills.frontend_developer;

  const renderContent = () => {
    switch (activeTab) {
      case 'dashboard':
        return (
          <DashboardContent>
            <OverviewSection>
              <ScoreCard>
                <ScoreValue>68%</ScoreValue>
                <ScoreLabel>Compatibilità con il mercato</ScoreLabel>
                <ScoreDescription>Basato sul ruolo di {roles.find(r => r.id === selectedRole)?.name}</ScoreDescription>
              </ScoreCard>
              <RadarChart />
            </OverviewSection>
            <SkillsGapSection>
              <SectionTitle>Competenze da Migliorare</SectionTitle>
              <SkillsList>
                {currentSkills.filter(s => s.level < s.required).map(skill => (
                  <SkillItem key={skill.name}>
                    <SkillName>{skill.name}</SkillName>
                    <SkillLevels>
                      <LevelLabel>Attuale:</LevelLabel>
                      <ProgressBar value={(skill.level / 5) * 100} />
                      <LevelLabel>Richiesto:</LevelLabel>
                      <ProgressBar value={(skill.required / 5) * 100} />
                    </SkillLevels>
                    <SkillGap>Gap: {skill.required - skill.level} livelli</SkillGap>
                  </SkillItem>
                ))}
              </SkillsList>
            </SkillsGapSection>
            <RecommendationsSection>
              <SectionTitle>Corsi Consigliati</SectionTitle>
              {/* Placeholder for course recommendations */}
              <p>Raccomandazioni corsi qui...</p>
            </RecommendationsSection>
          </DashboardContent>
        );
      case 'assessment':
        return (
          <AssessmentContent>
            <SectionTitle>Questionario di Autovalutazione</SectionTitle>
            <p>Seleziona il tuo livello per ogni competenza richiesta per il ruolo di {roles.find(r => r.id === selectedRole)?.name}:</p>
            <AssessmentForm>
              {currentSkills.map(skill => (
                <SkillAssessmentItem key={skill.name}>
                  <SkillAssessmentName>{skill.name}</SkillAssessmentName>
                  <LevelSelector>
                    {[1, 2, 3, 4, 5].map(level => (
                      <LevelButton key={level} active={skill.level === level}>{level}</LevelButton>
                    ))}
                  </LevelSelector>
                </SkillAssessmentItem>
              ))}
              <Button primary>Salva Valutazione</Button>
            </AssessmentForm>
          </AssessmentContent>
        );
      case 'plan':
        return (
          <PlanContent>
            <SectionTitle>Piano di Sviluppo Personale</SectionTitle>
            <p>Crea e monitora il tuo piano per colmare i gap.</p>
            {/* Placeholder for development plan */}
          </PlanContent>
        );
      default:
        return null;
    }
  };

  return (
    <GapAnalyzerContainer>
      <Sidebar />
      <MainContent>
        <Header />
        <ContentContainer>
          <PageHeader>
            <PageTitle>Gap Analyzer</PageTitle>
            <RoleSelector>
              <label htmlFor="role-select">Ruolo selezionato:</label>
              <Select id="role-select" value={selectedRole} onChange={(e) => setSelectedRole(e.target.value)}>
                {roles.map(role => (
                  <option key={role.id} value={role.id}>{role.name}</option>
                ))}
              </Select>
            </RoleSelector>
          </PageHeader>
          
          <Tabs>
            <TabButton active={activeTab === 'dashboard'} onClick={() => setActiveTab('dashboard')}>Dashboard</TabButton>
            <TabButton active={activeTab === 'assessment'} onClick={() => setActiveTab('assessment')}>Autovalutazione</TabButton>
            <TabButton active={activeTab === 'plan'} onClick={() => setActiveTab('plan')}>Piano di Sviluppo</TabButton>
          </Tabs>
          
          <TabContent>
            {renderContent()}
          </TabContent>
        </ContentContainer>
      </MainContent>
    </GapAnalyzerContainer>
  );
};

